.image-container {
    width: 200px;
    height: 200px;
    margin: 0 auto;
    animation: fadeIntoView 1.8s;
  }
  
  .image-container img {
    width: 100%;
    border-radius: 50%;
  }

  @keyframes fadeIntoView {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }