.link {
    text-align: center;
    margin: 24px 0 12px 0;
}

.link a {
    text-decoration: none;
    font-weight: bold;
    font-size: 24px;
    border-bottom: 2px solid transparent;
    color: black;
}

@media (hover: hover) {
    .link a:hover {
        border-bottom: 2px solid black;
    }
}
